// 
const BASE_URL = 'http://localhost:8081'

// request function
const request = (options) => {
	return new Promise((resolve,reject) => {
		uni.request({
			url: BASE_URL + options.url,
			method: options.method || 'GET',
			data: options.data || {},
			success: (res) => {
				resolve(res)
			},
			fail: (err) => {
				uni.showToast({
					title: ''
				})
				reject(err)
			}
		})
	})
}

export default request